<template>
  <!-- /continue -->
  <!-- html and js autoinjects to App.vue (and therefore on public/index.html) -->
  <v-container
    v-bind:class="{
      mobile: $vuetify.breakpoint.width < 800,
      browser: $vuetify.breakpoint.width >= 800,
      wide: $vuetify.breakpoint.width >= 1200,
    }"
  >
    <v-card
      style="background: transparent; padding: 20px"
      elevation="10"
      outlined
      :dark="$store.getters.getTheme"
    >
      <!-- if there are errors, show this div -->
      <v-card
        :dark="$store.getters.getTheme"
        id="errordiv"
        elevation="20"
        v-if="errors.length"
      >
        <v-alert type="error"> Korjaa seuraavat virheet: </v-alert>
        <v-list>
          <v-list-item v-for="(error, index) in errors" v-bind:key="index">
            <v-list-item-icon>
              <v-icon color="red">mdi-alert-circle</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>{{ error }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-card>
      <v-tabs
        v-model="tab"
        background-color="blue lighten-2"
        color="blue darken-4"
        grow
        show-arrows
        next-icon="mdi-arrow-right-bold-box-outline"
        prev-icon="mdi-arrow-left-bold-box-outline"
        center-active
      >
        <v-tabs-slider color="blue darken-4"></v-tabs-slider>
        <v-tab href="#competitions">Kilpailut</v-tab>
        <v-tab href="#cups">Cupit</v-tab>
      </v-tabs>
      <v-tabs-items v-model="tab" style="background: transparent">
        <v-tab-item :value="'competitions'">
          <v-row>
            <v-col>
              <h1>Kilpailut</h1>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <div v-if="competitions.length" class="scroll_table">
                <v-card :dark="$store.getters.getTheme">
                  <v-card-title>
                    <p class="flow-text">Kilpailut</p>
                    <v-spacer></v-spacer>
                    <v-text-field
                      :dark="$store.getters.getTheme"
                      v-model="search_comp"
                      append-icon="mdi-magnify"
                      label="Hae kilpailua"
                      single-line
                      hide-details
                    ></v-text-field>
                  </v-card-title>
                  <v-data-table
                    id="normal-table"
                    :headers="headers"
                    :items="competitions"
                    :search="search_comp"
                  >
                    <template v-slot:[`item.start_date`]="{ item }">
                      <v-chip color="primary darken-2">{{
                        item.start_date.format("DD.MM.YYYY")
                      }}</v-chip>
                    </template>
                    <template v-slot:[`item.cup_name`]="{ item }">
                      <v-chip
                        >{{ item.cup_name }} ({{
                          item.start_date.format("YYYY")
                        }})</v-chip
                      >
                    </template>
                    <template v-slot:[`item.cup_points_multiplier`]="{ item }">
                      <v-chip
                        :color="getColor(item.cup_points_multiplier)"
                        :outlined="$store.getters.getTheme"
                        >{{ item.cup_points_multiplier }}x</v-chip
                      >
                    </template>
                    <template v-slot:[`item.choose`]="{ item }">
                      <v-tooltip right>
                        <template v-slot:activator="{ on, attrs }">
                          <v-btn
                            color="primary"
                            v-bind="attrs"
                            v-on="on"
                            @click="pickCompetition(item)"
                            ><v-icon color="black"
                              >mdi-arrow-right-bold-box-outline</v-icon
                            ></v-btn
                          >
                        </template>
                        <span>Siirry kilpailuun</span>
                      </v-tooltip>
                    </template>
                  </v-data-table>
                </v-card>
              </div>
              <div v-else>
                <v-col v-if="!loading">
                  <v-row>
                    <v-col v-if="cups.length">
                      <h2
                        v-bind:class="{
                          'white--text': $store.getters.getTheme,
                        }"
                      >
                        Ei kilpailuja!
                      </h2>
                      <router-link to="/register-comp">
                        <v-btn tile color="blue lighten-1"
                          ><v-icon>mdi-plus-cicle</v-icon>Luo kilpailu!</v-btn
                        >
                      </router-link>
                    </v-col>
                    <v-col v-else>
                      <h2
                        v-bind:class="{
                          'white--text': $store.getters.getTheme,
                        }"
                      >
                        Ei kilpailuja!
                      </h2>
                      <h2
                        v-bind:class="{
                          'white--text': $store.getters.getTheme,
                        }"
                      >
                        Luo Cup ensin
                      </h2>
                      <v-btn tile color="blue lighten-1" @click="tab = 'create'"
                        ><v-icon>mdi-plus-circle</v-icon>Luo Cup!</v-btn
                      >
                    </v-col>
                  </v-row>
                </v-col>
                <v-col v-else-if="error">
                  <h2 class="error">{{ error }}</h2>
                </v-col>
                <v-col v-else>
                  <h2
                    v-bind:class="{
                      'white--text': $store.getters.getTheme,
                    }"
                  >
                    Ladataan kilpailuja...
                  </h2>
                  <ProgressBarQuery />
                </v-col>
              </div>
              <v-col v-if="competitions.length" style="margin-top: 20px">
                <router-link to="/register-comp">
                  <v-btn tile color="white" outlined large
                    ><v-icon>mdi-plus-circle</v-icon>Luo uusi kilpailu!</v-btn
                  >
                </router-link>
              </v-col>
            </v-col>
          </v-row>
        </v-tab-item>
        <v-tab-item :value="'cups'">
          <v-row>
            <v-col>
              <h1>Cupit</h1>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <div v-if="cups.length" class="scroll_table">
                <v-card :dark="$store.getters.getTheme">
                  <v-card-title>
                    <p class="flow-text">Cupit</p>
                    <v-spacer></v-spacer>
                    <v-text-field
                      v-model="search_cup"
                      append-icon="mdi-magnify"
                      label="Hae cuppia"
                      single-line
                      hide-details
                    ></v-text-field>
                  </v-card-title>
                  <v-data-table
                    id="normal-table"
                    :headers="headers_cup"
                    :items="cups"
                    :search="search_cup"
                    :loading="publishing"
                  >
                    <template v-slot:[`item.name`]="{ item }">
                      <v-chip color="primary darken-3">{{ item.name }}</v-chip>
                    </template>
                    <template v-slot:[`item.year`]="{ item }">
                      <v-chip
                        color="grey"
                        :outlined="$store.getters.getTheme"
                        >{{ item.year }}</v-chip
                      >
                    </template>
                    <template v-slot:[`item.isPublic`]="{ item }">
                      <v-tooltip right>
                        <template v-slot:activator="{ on, attrs }">
                          <v-chip
                            v-bind="attrs"
                            v-on="on"
                            :outlined="$store.getters.getTheme"
                            :color="
                              item.isPublic ? 'green darken-2' : 'red darken-2'
                            "
                            @click="publishCup(item)"
                            :disabled="publishing"
                            >{{
                              item.isPublic ? "Julkinen" : "Salainen"
                            }}</v-chip
                          >
                        </template>
                        <span>Muuta tila painamalla</span>
                      </v-tooltip>
                    </template>
                    <template v-slot:[`item.choose`]="{ item }">
                      <v-tooltip right>
                        <template v-slot:activator="{ on, attrs }">
                          <v-btn
                            color="primary"
                            v-bind="attrs"
                            v-on="on"
                            @click="pickCup(item)"
                            ><v-icon color="black"
                              >mdi-arrow-right-bold-box-outline</v-icon
                            ></v-btn
                          >
                        </template>
                        <span>Siirry Cuppiin</span>
                      </v-tooltip>
                    </template>
                  </v-data-table>
                </v-card>
              </div>
              <v-row v-else>
                <v-col v-if="error" class="error"
                  ><h2>{{ error }}</h2></v-col
                >
                <v-col v-else>
                  <h2
                    v-bind:class="{
                      'white--text': $store.getters.getTheme,
                    }"
                  >
                    Ladataan Cuppeja...
                  </h2>
                  <ProgressBarQuery />
                </v-col>
              </v-row>
            </v-col>
          </v-row>
          <v-row style="margin-bottom: 20px">
            <v-col>
              <div class="text-center">
                <v-dialog v-model="createCupDialog" width="700" persistent>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      tile
                      color="white"
                      outlined
                      large
                      v-bind="attrs"
                      v-on="on"
                      :disabled="loading"
                    >
                      <v-icon>mdi-plus-circle</v-icon>Luo uusi cup!
                    </v-btn>
                  </template>

                  <v-card :dark="$store.getters.getTheme">
                    <v-card-title> Luo Cup </v-card-title>

                    <v-row align="center" justify="center">
                      <v-col cols="8" class="input-fields">
                        <v-text-field
                          :dark="$store.getters.getTheme"
                          label="Cupin nimi"
                          v-model="name"
                          :maxlength="40"
                          :loading="loading"
                          :counter="40"
                        />
                      </v-col>
                    </v-row>
                    <v-row align="center" justify="center">
                      <v-col cols="6" class="input-fields">
                        <v-menu
                          ref="menu"
                          :close-on-content-click="false"
                          v-model="menu"
                          :nudge-right="40"
                          transition="scale-transition"
                          offset-y
                          min-width="290px"
                        >
                          <template v-slot:activator="{ on, attrs }">
                            <v-text-field
                              :dark="$store.getters.getTheme"
                              v-model="year"
                              label="Vuosi"
                              prepend-icon="event"
                              readonly
                              v-bind="attrs"
                              v-on="on"
                            ></v-text-field>
                          </template>
                          <v-date-picker
                            ref="picker"
                            v-model="year"
                            @input="saveYear"
                            min="2000-01-01"
                            reactive
                            no-title
                          ></v-date-picker>
                        </v-menu>
                      </v-col>
                    </v-row>
                    <v-row align="center" justify="center">
                      <v-col cols="6" class="input-fields">
                        <v-checkbox
                          v-model="isThereCompetitionLimit"
                          label="Aseta merkitsevien kilpailujen mr?"
                        ></v-checkbox>
                      </v-col>
                    </v-row>
                    <v-row
                      align="center"
                      justify="center"
                      v-if="isThereCompetitionLimit"
                    >
                      <v-col cols="6" class="input-fields">
                        <v-text-field
                          v-model="meaningful_competitions"
                          :dark="$store.getters.getTheme"
                          label="Merkitsevien kilpailujen mr"
                          maxlength="3"
                          type="number"
                          @paste.prevent
                          counter="3"
                          min="1"
                          max="100"
                          @keypress="isNumber($event, true)"
                          :rules="number_rules"
                          :loading="loading"
                        />
                      </v-col>
                    </v-row>
                    <v-row align="center" justify="center">
                      <v-col>
                        <v-btn
                          tile
                          color="primary"
                          @click="saveCup"
                          :loading="loading"
                          ><v-icon>mdi-check</v-icon>Luo Cup</v-btn
                        >
                      </v-col>
                    </v-row>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn
                        color="primary"
                        text
                        @click="createCupDialog = false"
                      >
                        Peruuta
                      </v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </div>
            </v-col>
          </v-row>
        </v-tab-item>
      </v-tabs-items>
      <v-snackbar v-model="snackbar" :timeout="timeout">
        {{ text }}

        <template v-slot:action="{ attrs }">
          <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">
            Close
          </v-btn>
        </template>
      </v-snackbar>
    </v-card>
  </v-container>
</template>

<script>
import ProgressBarQuery from "../components/layout/ProgressBarQuery";
import CompetitionService from "../CompetitionService";
import CupService from "../CupService";

export default {
  name: "ContinueComp",
  data() {
    return {
      tab: null,
      competitions: [],
      headers: [
        { text: "Kilpailun Pivmr", value: "start_date" },
        { text: "Nimi", value: "name" },
        { text: "Cup", value: "cup_name" },
        { text: "Pistekerroin", value: "cup_points_multiplier" },
        { text: "Valitse", value: "choose", sortable: false },
      ],
      headers_cup: [
        { text: "Nimi", value: "name" },
        { text: "Vuosi", value: "year" },
        { text: "Julkisuus", value: "isPublic" },
        { text: "Valitse", value: "choose", sortable: false },
      ],
      error: "",
      competition_input: null,
      loading: false,
      publishing: false,
      cups: [],
      name: null,
      year: null,
      menu: false,
      errors: [],
      search_comp: "",
      search_cup: "",
      snackbar: false,
      text: "",
      timeout: 5000,
      createCupDialog: false,
      number_rules: [
        (value) => !!value || "Kentt ei voi jd tyhjksi!",
        (value) => !isNaN(value || "") || "Ei ole numero!",
      ],
      meaningful_competitions: 1,
      isThereCompetitionLimit: false,
    };
  },
  watch: {
    menu(val) {
      val && this.$nextTick(() => (this.$refs.picker.activePicker = "YEAR"));
    },
  },
  components: {
    ProgressBarQuery,
  },
  async created() {
    this.loading = true;
    const user = JSON.parse(localStorage.getItem("user"));
    const user_id = user["_id"];
    // Get competitions
    const query = { user_id: user_id };
    await CompetitionService.getCompetitions(query)
      .then((response) => {
        this.competitions = response;
        // Convert dates to moment objects
        this.competitions.forEach((competition) => {
          competition.start_date = this.$moment(competition.start_date);
          competition.end_date = this.$moment(competition.end_date);
        });
        // Sort them based on start_date so the oldest competitions are the last
        this.competitions.sort(function compare(a, b) {
          return b.start_date.isAfter(a.start_date);
        });
      })
      .catch((err) => {
        if (err.response) {
          if (err.response.status === 408) {
            this.loading = false;
            return console.log(err);
          } else {
            this.loading = false;
            return console.log(err);
          }
        } else {
          return console.log(err);
        }
      });

    // Get Cups
    await CupService.getCups({
      user_id: user_id,
    })
      .then((response) => {
        this.cups = response;
        this.cups.sort((a, b) => {
          return this.$moment(b.year).isAfter(this.$moment(a.year));
        });
      })
      .catch((err) => {
        if (err.response) {
          if (err.response.status === 400) {
            console.log(err);
            return this.showError("Bad request!");
          }
          if (err.response.status === 408) {
            return this.showError(
              "Yhteytt tietokantaan ei saatu... Yrit uudelleen pivittmll sivu (Connection timed out)"
            );
          } else {
            return console.log(err);
          }
        } else {
          return console.log(err);
        }
      });
    this.loading = false;
  },
  mounted() {
    if (this.$route.query.tab) {
      this.tab = this.$route.query.tab;
    }
    // Focus on top of the page when changing pages
    location.href = "#";
    location.href = "#app";
  },
  methods: {
    //filter other characters out for number inputs
    isNumber: function (evt, isDate) {
      var charToCheckCode = 46; // --> .
      var charToCheck = ".";

      if (!isDate) {
        charToCheckCode = 58; // --> :
        charToCheck = ":";
      }

      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (
        charCode > 31 &&
        (charCode < 48 || charCode > 57) &&
        charCode !== charToCheckCode
      ) {
        evt.preventDefault();
      } else {
        if (charCode == charToCheckCode) {
          if (evt.target.value.indexOf(charToCheck) >= 0) {
            evt.preventDefault();
          } else {
            return true;
          }
        } else {
          return true;
        }
      }
    },
    getColor(multiplier) {
      if (multiplier > 1) return "red";
      if (multiplier === 1) return "green";
      else return "grey";
    },
    async publishCup(cup) {
      cup.isPublic = !cup.isPublic;
      try {
        //TODO update only this one variable (competition.normal_points) to database, not the whole competition
        this.publishing = true;
        const newValues = {
          $set: { isPublic: cup.isPublic },
        };
        await CupService.updateValues(cup.id, newValues);
      } catch (err) {
        console.error(err.message);
      }
      this.publishing = false;
    },
    // Add error to error array and direct user to it
    showError: function (error) {
      this.errors.push(error);
      location.href = "#";
      location.href = "#app";
    },
    pickCompetition: function (competition) {
      // Pick competition for the app to use
      //NOTE Store competition to vuex, redundant?
      this.$store.state.competition = competition;
      // Set competition._id to localstorage for database queries
      localStorage.setItem(
        "competition",
        JSON.stringify({
          id: competition._id,
          start_date: competition.start_date,
          end_date: competition.end_date,
        })
      );
      // redirect to /overview
      this.$router.push({ path: "/overview" });
    },
    pickCup: function (cup) {
      // Pick cup for the app to use
      // Set cup.id to localstorage for database queries
      localStorage.setItem("cup", cup.id);
      // redirect to /cup-overview
      this.$router.push({ path: "/cup-overview" });
    },
    //TODO dropdown to select current year as highlighted when opened
    saveYear(year) {
      year = year.split("-")[0];
      this.year = year;
      this.$refs.picker.activePicker = "YEAR";
      this.menu = false;
    },
    async saveCup() {
      this.errors = [];
      this.loading = true;

      if (!this.name) {
        this.showError("Syt Cupille nimi!");
      }
      if (!this.year) {
        this.showError("Valitse Cupille vuosi!");
      }

      if (this.isThereCompetitionLimit && !this.meaningful_competitions) {
        this.showError("Syt merkitsevien kilpailujen mr!");
      }

      if (!this.errors.length) {
        const user = JSON.parse(localStorage.getItem("user"));
        const user_id = user["_id"];
        const cup = {
          user_id: user_id,
          name: this.name,
          year: this.year,
          meaningful_competitions: this.isThereCompetitionLimit
            ? this.meaningful_competitions
            : -1,
        };
        try {
          //Submit Cup to database (check 'client\src\CupService.js' and 'server\routes\api\cups.js' to see how this works)
          await CupService.insertCup(cup);
          this.text = "Cup listty tietokantaan!";
          this.snackbar = true;
          this.cups = await CupService.getCups({
            user_id: user_id,
          });
          this.loading = false;
          this.year = null;
          this.name = null;
          this.tab = "cups";
        } catch (err) {
          this.errors.push(err.message);
        }
      }
      this.loading = false;
      this.createCupDialog = false;
      this.isThereCompetitionLimit = false;
      this.meaningful_competitions = 1;
    },
  },
};
</script>

<style></style>
